import React, { useState } from "react";
import validator from "validator";
import "../../sass/forms/registration.scss";

export default ({ submit }) => {
  const initialState = {
    firstName: "JoÃ£o",
    lastName: "Narciso",
    email: "joao@email.com",
    picture: null,
    doc: "",
  };
  const [data, setData] = useState(initialState);
  const [errors, setErrors] = useState({});
  const [success] = useState(false);

  const handleChange = (e) => {
    const { name, value, type } = e.target;

    if (type === "file") {
      setData({ ...data, [name]: e.target.files[0] });
    } else {
      setData({ ...data, [name]: value });
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const errors = validate(data);

    if (Object.keys(errors).length > 0) {
      setErrors(errors);
    } else {
      const formData = new FormData();
      formData.append("picture", data.picture);
      console.log(formData.getAll("picture"));
      console.log(data.picture);
      //submit({ ...data, picture: formData });
      // const user = {
      //   first_name: data.firstName,
      //   last_name: data.lastName,
      //   email: data.email,
      //   doc: formData
      // };
    }
  };

  const validate = (data) => {
    const errors = {};

    if (!data.firstName) {
      errors.firstName = "Enter your first name.";
    } else if (!validator.isLength(data.firstName, { min: 2, max: 50 })) {
      errors.firstName = "Min 2 characters and max 50 characters.";
    }

    if (!data.lastName) {
      errors.lastName = "Enter your last name.";
    } else if (!validator.isLength(data.lastName, { min: 2, max: 50 })) {
      errors.name = "Min 2 characters and max 50 characters.";
    }
    if (!data.email) {
      errors.email = "Enter an E-mail address.";
    } else if (!validator.isEmail(data.email)) {
      errors.email = "Enter a valid E-mail address.";
    }

    if (!data.doc) {
      errors.doc = "Please provide a valid Government ID";
    } else if (!validator.isMimeType(data.doc.type)) {
      errors.doc = "Please provide a valid file ( pdf, png or jpg ).";
    }
    if (!data.picture) {
      errors.picture = "Please provide a valid Government ID";
    } else if (!validator.isMimeType(data.picture.type)) {
      errors.picture = "Please provide a valid file ( pdf, png or jpg ).";
    }

    return errors;
  };
  !!errors.firstName && console.log({ errors });

  return (
    <div className="container py-4 my-4" id="registration">
      <h3>Register now</h3>
      <form onSubmit={handleSubmit}>
        {success && (
          <div className="alert alert-success" role="alert">
            Your information has been sent!
          </div>
        )}
        {!!errors.global && (
          <div className="alert alert-danger" role="alert">
            ${errors.global}
          </div>
        )}
        <div className="form-group">
          <label htmlFor="firstName">First name</label>
          <input
            type="text"
            className={`form-control ${!!errors.firstName ? "is-invalid" : ""}`}
            id="firstName"
            placeholder="First Name"
            name="firstName"
            value={data.firstName}
            onChange={handleChange}
          />

          {!!errors.firstName && (
            <div className="invalid-feedback">{errors.firstName}</div>
          )}
        </div>
        <div className="form-group">
          <label htmlFor="lastName">Last name</label>
          <input
            type="text"
            className={`form-control ${!!errors.lastName ? "is-invalid" : ""}`}
            id="lastName"
            name="lastName"
            placeholder="Last Name"
            value={data.lastName}
            onChange={handleChange}
          />
          {!!errors.lastName && (
            <div className="invalid-feedback">{errors.lastName}</div>
          )}
        </div>
        <div className="form-group">
          <label htmlFor="email">E-mail address</label>
          <input
            type="email"
            className={`form-control ${!!errors.email ? "is-invalid" : ""}`}
            id="email"
            placeholder="E-mail Address"
            name="email"
            value={data.email}
            onChange={handleChange}
          />
          {!!errors.email && (
            <div className="invalid-feedback">{errors.email}</div>
          )}
        </div>
        <div className="form-group">
          <label htmlFor="doc">Government ID</label>
          <input
            type="file"
            accept="image/png,image/jpg, application/pdf"
            className={`form-control form-control-file ${
              !!errors.doc ? "is-invalid" : ""
            }`}
            value=""
            onChange={handleChange}
            id="doc"
            name="doc"
          />

          {!!errors.picture ? (
            <div className="invalid-feedback">{errors.picture}</div>
          ) : (
            <small id="emailHelp" className="form-text text-danger">
              Invalid file format.
            </small>
          )}
        </div>{" "}
        <div className="form-group">
          <label htmlFor="picture">Profile picture</label>
          <input
            type="file"
            accept="image/png,image/jpg, application/pdf"
            className={`form-control form-control-file ${
              !!errors.picture ? "is-invalid" : ""
            }`}
            value=""
            onChange={handleChange}
            id="picture"
            name="picture"
          />

          {!!errors.doc ? (
            <div className="invalid-feedback">{errors.doc}</div>
          ) : (
            <small id="emailHelp" className="form-text text-danger">
              Invalid file format.
            </small>
          )}
        </div>
        <button
          type="submit"
          disabled={success ? true : false}
          className={`btn ${success ? "disabled" : ""}`}
        >
          Register
        </button>
      </form>
    </div>
  );
};
