import React, { useContext } from "react";
import { Route, Redirect } from "react-router-dom";
import Context from "../context";

export default (props) => {
  const { state } = useContext(Context);
  return !state.user.token ? (
    <Redirect
      to={{
        pathname: "/login",
      }}
    />
  ) : (
    <Route {...props} />
  );
};
