import React, { useContext, useState } from "react";
import axios from "axios";
import Context from "../context";
import config from "../config";

import Task from "../components/Forms/Task";

export default function AddTask() {
  const { state } = useContext(Context);
  const [validation, setValidation] = useState({});

  const submit = async (data) => {
    try {
      const response = await axios.post(`${config.API_URL}/tasks`, data, {
        headers: { Authorization: `bearer ${state.user.token}` },
      });

      setValidation({ type: "success", message: response.data.message });
    } catch (ex) {
      setValidation({ type: "danger", message: ex.response.data.message });
    }
  };

  return (
    <div className="container">
      {validation.type !== "success" && <Task submit={submit} />}

      {Object.keys(validation).length > 0 && (
        <div className={`alert alert-${validation.type}`}>
          {validation.message}
        </div>
      )}
    </div>
  );
}
