import React, { useState, useContext, useEffect } from "react";
import Context from "../context";
import axios from "axios";
import config from "../config";
import Applicants from "./Cards/Applicants";

const FulfilModal = ({ task, setFulfilment, fulfil }) => {
  const { state } = useContext(Context);

  const [applicants, setApplicants] = useState([]);

  // console.log({ user: state.user });

  const capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1) + " ";
  };

  // useEffect(() => {
  //   if (state.user.user_id === task.user_id) {
  //     fetchApplicants();
  //   }
  // }, [state.user, task]);

  useEffect(() => {
    const fetchApplicants = async () => {
      const req = await axios.get(`${config.API_URL}/applicants/${task.id}`, {
        headers: { Authorization: `bearer ${state.user.token}` },
      });

      setApplicants(req.data);
      console.log("app data", req);
      console.log("Request applicants...");
    };

    fetchApplicants();
  }, [state.user.token, task.id]);

  // /v1/tasks/2/applicants
  // /v1/applicants/2

  return (
    <div className="modal d-block" tabIndex="-1">
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">
              {task.id} -
              {task.task_type
                .split("-")
                .map((string) => capitalizeFirstLetter(string))}
            </h5>
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={() => setFulfilment({})}
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <p>{task.short_description}</p>
          </div>
          <div className="modal-footer d-block">
            <div className="d-flex justify-content-between">
              <small className="text-muted">{task.created_at}</small>
              <span>
                {applicants ? Object.keys(applicants).length : 0} applicants
              </span>
            </div>
            {state.user.user_id === task.user_id ? (
              <Applicants applicants={applicants} />
            ) : (
              <button
                type="button"
                className="btn btn-primary"
                onClick={fulfil}
              >
                Fulfil
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};
export default FulfilModal;
