import React, { useRef, useEffect } from "react";

const Map = ({ tasks, setFulfilment }) => {
  const mapRef = useRef();

  const addMarkers = (map) => {
    tasks.map((task) => {
      let icon =
        "https://unpkg.com/ionicons@5.2.3/dist/svg/people-circle-outline.svg";
      icon =
        task.task_type === "material-need"
          ? "https://unpkg.com/ionicons@5.2.3/dist/svg/bag-remove-outline.svg"
          : icon;

      const marker = new window.google.maps.Marker({
        position: { lat: task.lat, lng: task.lng },
        map,
        title: task.task_type,
        icon: {
          url: icon,
          scaledSize: new window.google.maps.Size(24, 24), // scaled size
        },
      });
      marker.addListener("click", () => {
        setFulfilment(task);
      });

      return marker;
    });
  };

  useEffect(() => {
    if (window.google) {
      const map = new window.google.maps.Map(mapRef.current, {
        center: { lat: -34.397, lng: 150.644 },
        zoom: 12,
      });

      // navigator.geolocation.getCurrentPosition(({ coords }) => {
      //   console.log({ coords });
      // });

      navigator.geolocation.getCurrentPosition((position) => {
        console.log({ map });

        console.log({
          position,
        });
        map.setCenter({
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        });
      });

      addMarkers(map);

      // tasks.length > 0 &&
      //   map.setCenter({ lat: tasks[0].lat, lng: tasks[0].lng });
    }
  }, [window.google]);

  return <div ref={mapRef} className={"w-100"} style={{ height: 500 }}></div>;
};

export default Map;
