import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Header from "./components/Sections/Header";
import Footer from "./components/Sections/Footer";
import { Home, Login, Register, AddTask } from "./pages";
import ProtectiveRoute from "./components/AuthRoute";
import "./sass/global.scss";

function App() {
  return (
    <div className="App">
      <Router>
        <Header />
        <ProtectiveRoute exact path="/" component={Home} />
        <Route path="/register" component={Register} />
        <ProtectiveRoute path="/home" component={Home} />
        <Route path="/login" component={Login} />
        <ProtectiveRoute path="/addtask" component={AddTask} />
        <Footer />
      </Router>
    </div>
  );
}

export default App;
