import React, { useState, useEffect, useRef } from "react";
import moment from "moment";

export default ({ submit }) => {
  const mapRef = useRef();

  const [current, setCurrent] = useState({ lat: 51.2, lng: 6.78 });
  const [data, setData] = useState({
    date: "",
    task_type: "material-need",
    short_description:
      "This is the task's short description, with minimum of 50 characters",
  });
  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;

    setData({ ...data, [name]: value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();

    const errors = validate();
    if (Object.keys(errors).length > 0) {
      setErrors(errors);
    } else {
      submit(data);
    }
  };

  const handleLocationChange = (marker) => {
    window.google.maps.event.addListener(marker, "dragend", function (e) {
      setTimeout(() => {
        console.log({ e });
      }, 500);
    });
    console.log({ marker }); // Set new location
    // setData({ ...data, location: position });
  };

  const validate = () => {
    const errors = {};

    if (!data.short_description) {
      errors.short_description = "Enter your task's short description";
    }
    if (
      data.short_description.length < 50 ||
      data.short_description.length > 300
    ) {
      errors.short_description =
        "The short description has to be between 50 and 300 characters long.";
    }
    if (!data.task_type) {
      errors.task_type = "Select the type of task";
    }
    return errors;
  };

  useEffect(() => {
    const map = new window.google.maps.Map(mapRef.current, {
      center: { lat: -34.397, lng: 150.644 },
      zoom: 10,
    });

    navigator.geolocation.getCurrentPosition((position) => {
      const location = {
        lat: position.coords.latitude,
        lng: position.coords.longitude,
      };
      setCurrent(location);
      map.setCenter(location);
      const marker = new window.google.maps.Marker({
        position: location,
        map,
        draggable: true,
      });
      handleLocationChange(marker);

      setData({
        ...data,
        ...location,
      });
    });
  }, [window.google]);

  console.log({ errors, data });
  console.log("current", current);
  return (
    <section className="py-5">
      <h2>Add a new request</h2>

      <form onSubmit={handleSubmit}>
        <div className="row">
          <div className="col-md-6 mb-3">
            <label htmlFor="task_type">Type</label>
            <select
              name="task_type"
              className={`form-control ${
                !!errors.task_type ? `is-invalid` : ""
              }`}
              id="task_type"
              onChange={handleChange}
              defaultValue={data.task_type}
            >
              <option disabled value="">
                Select
              </option>
              <option value="one-time-task">One-time task</option>
              <option value="material-need">Material need</option>
            </select>
            {!!errors.task_type && (
              <div className="invalid-feedback">{errors.task_type}</div>
            )}
          </div>
          <div className="col-md-6 mb-3">
            <label htmlFor="datetime">Date/Time</label>
            <input
              className={`form-control`}
              onChange={handleChange}
              id="datetime"
              type="datetime"
              name="date"
              type="datetime-local"
              min={moment().format("YYYY[-]MM[-]DD[T]HH:m")}
            />
          </div>
          {console.log(moment().format())}
          <div className="col-md-12 mb-3">
            <label htmlFor="short_description">Description</label>
            <textarea
              name="short_description"
              className={`form-control ${
                !!errors.short_description ? `is-invalid` : ""
              }`}
              id="short_description"
              placeholder=""
              onChange={handleChange}
              value={data.short_description}
              maxLength="300"
              minLength="50"
              //required=""
            />
            {!!errors.short_description && (
              <div className="invalid-feedback">{errors.short_description}</div>
            )}
          </div>
        </div>
        <div className="col-md-12">
          <strong>Please pick your task's location</strong>
          <div ref={mapRef} style={{ height: "300px" }} />
        </div>
        <hr className="mb-4" />
        <button className="btn btn-primary btn-lg btn-block" type="submit">
          Submit
        </button>
      </form>
    </section>
  );
};
